.mainWrapper {
    @apply flex flex-row justify-between items-center px-2 py-2;
    @apply md:px-3;
}

.logoImage {
    @apply h-5 w-full;
    @apply md:h-8;
    @apply xl:h-10;
}

.mobileMenu {
    @apply block text-3xl mr-5;
    @apply md:text-4xl;
    @apply lg:text-3xl;
    @apply xl:hidden;
}

.linkIcon {
    @apply text-xl ml-1.5;
}

.mainLinksWrapper {
    @apply flex flex-row justify-between items-center ml-auto mr-5;
}

.mainLinks {
    @apply relative text-[15px] font-bold ml-3.5 hidden flex-row items-center justify-between p-2 py-3;
    @apply xl:flex
}

.mainMenu1 {
    @apply absolute z-10 flex flex-col top-2 w-44 invisible h-auto opacity-0 shadow-none drop-shadow-none p-2 rounded duration-500 ease-in-out;
}

.subMenu1 {
    @apply absolute ml-12 left-3/4 -top-2 z-20 flex flex-col top-0 w-52 invisible h-auto opacity-0 shadow-none drop-shadow-none p-2 rounded duration-500 ease-in-out;
}

.subMenu2 {
    @apply absolute ml-12 left-3/4 -top-2 z-20 flex flex-col top-0 w-52 invisible h-auto opacity-0 shadow-none drop-shadow-none p-2 rounded duration-500 ease-in-out;
}

.mainHover1:hover .mainMenu1 {
    @apply visible h-auto w-44 top-9 opacity-100 shadow-md;
}

.topBorder {
    @apply w-0 h-0.5 duration-500 ease-in-out -mx-2 -mt-0.5;
}

.mainHover1:hover .topBorder {
    @apply w-44 h-0.5;
}

.subHover1:hover .subMenu1 {
    @apply visible h-auto w-52 ml-12 left-3/4 -top-2 opacity-100 shadow-md;
}

.topBorder1 {
    @apply w-0 h-0.5 duration-500 ease-in-out -mx-2 -mt-0.5;
}

.subHover1:hover .topBorder1 {
    @apply w-52 h-0.5;
}

.subHover2:hover .subMenu2 {
    @apply visible h-auto w-52 ml-12 left-3/4 -top-2 opacity-100 shadow-md;
}

.topBorder2 {
    @apply w-0 h-0.5 duration-500 ease-in-out -mx-2 -mt-0.5;
}

.subHover2:hover .topBorder2 {
    @apply w-52 h-0.5;
}

.subLinks {
    @apply relative mx-2 py-2.5 border-b-[0.5px] text-[15px];
}

.accountWrapper {
    @apply flex flex-row justify-between items-center mr-6;
}

.accountIcon {
    @apply mx-1 text-lg;
    @apply sm:text-xl;
    @apply md:text-2xl;
}

.accountLink {
    @apply mx-1 hidden text-opacity-80 font-bold duration-300 ease-in-out;
    @apply lg:flex;
}

.accountOr {
    @apply text-sm hidden text-opacity-80 font-semibold;
    @apply lg:flex;
}

.buttonWrapper {
    @apply flex flex-row justify-between items-center px-4 py-3 rounded-3xl shadow-md drop-shadow-lg;
}

.buttonText {
    @apply font-bold ml-1.5 text-xs whitespace-nowrap;
    @apply sm:text-sm;
    @apply md:text-base;
}


/* Mobile Menu */

.mobileMainWrapper {
    @apply fixed z-50 top-0 left-0 h-screen w-full duration-[20ms] ease-in-out shadow-md drop-shadow-lg overflow-y-scroll bg-opacity-60;
}

.mobileWrapper {
    @apply fixed z-50 top-0 left-0 h-screen w-80 duration-300 ease-in-out shadow-md drop-shadow-lg overflow-y-scroll;
    @apply md:w-[25rem];
}

.closeMenu {
    @apply sticky top-3 ml-auto mr-3 h-8 w-8 rounded-full flex justify-center items-center shadow-md drop-shadow-md;
}

.mobileLinksWrapper {
    @apply mt-8;
}

.mobileLinks {
    @apply flex flex-row flex-wrap justify-between items-center border-t-[0.5px] px-6 py-4 font-bold text-sm;
}

.mobileSubLinks {
    @apply transition-all duration-300 ease-in-out;
}

.mobileSubLinksInvisible {
    @apply max-h-0 invisible opacity-0 overflow-hidden;
}

.mobileSubLinksVisible {
    @apply max-h-[60rem] visible opacity-100 overflow-hidden;
}
