.formWrapper{
    @apply w-[500px]
}

.inputGroup{
    @apply mb-12 flex flex-col;
}

.inputText{
    @apply w-full border-0 border-b-[1px] border-b-gray-300 focus-within:border-b-[1px] px-0  focus-within:border-b-black focus-within:ring-0;
}

.inputLabel{
    @apply -mt-8 -translate-y-8;
}

.formButton{
    @apply flex w-full mt-8 mb-4 justify-center text-white px-10 py-3 bg-primary-teal  rounded-3xl hover:bg-opacity-80
}
.buttonIcon{
    @apply mt-[2px] mr-1
}

.formChecboxGroup{
    @apply flex
}

.googleButton{
    @apply flex w-full justify-center border-[1px] py-2 rounded-md mt-3
}